/*
 * jekyll-highlight-cards Structural Styles
 *
 * This file contains only structural/layout styles - no colors, borders, or visual effects.
 * Use this file when you want full control over the visual appearance.
 *
 * To use these styles, add to your site's main SCSS:
 *   @import "highlight-cards-structure";
 *
 * Then define your own color styles for the provided classes.
 */

/* ============================================================================
   Linkcard Styles
   ========================================================================= */

.link-card {
  padding: 1em 1.25em 1.5em 1.25em;
  text-align: center;
  position: relative;
}

.link-card-archive {
  position: absolute;
  right: 0.75rem;
  bottom: 0.5rem;
}

/* ============================================================================
   Polaroid Styles
   ========================================================================= */

.polaroid-container {
  display: block;
  width: 100%;
  text-align: center;
}

.polaroid {
  display: inline-block;
  position: relative;
  border: 1px solid; /* Width and style only - color set in colors file */
  padding: 10px 10px 25px 10px;
  margin: 1em;
  text-align: center;
  max-width: 100%;
  transition: box-shadow 0.2s ease, transform 0.2s ease;

  .polaroid-image {
    display: block;
    max-width: 100%;
    height: auto;
    border: 1px solid; /* Width and style only - color set in colors file */
    margin: 0 auto;
  }

  .polaroid-title {
    margin-top: 5px;
    padding: 0 10px;
  }

  .polaroid-link {
    margin-top: 5px;
    padding: 0 10px;
    word-break: break-all;
  }

  .polaroid-archive {
    position: absolute;
    right: 0.75rem;
    bottom: 0.5rem;
  }
}

/* ============================================================================
   Print Styles
   ========================================================================= */

@media print {
  .link-card,
  .polaroid {
    page-break-inside: avoid;
    border: 1px solid; /* Width and style only - color set in colors file */
  }
}

