/* ============================================================================
   Polaroid Color Styles
   ========================================================================= */

.polaroid {
  border-color: #333;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);

  .polaroid-image {
    border-color: #333;
  }
}

/* ============================================================================
   Print Styles
   ========================================================================= */

@media print {
  .link-card,
  .polaroid {
    box-shadow: none;
    border-color: #333;
  }
}

